.trending {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 30rem;
  padding: 0;
}

.trending h3 {
  font-size: 1.75rem;
  padding: 1rem;
}

.trending a {
  font-size: 1.5rem;
  text-decoration: none;
  padding: 0 2rem;
}

.trending ul {
  overflow-y: hidden;
  width: 100%;
  list-style: none;
  height: 20rem;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  background: transparent;
}

.trending ul::-webkit-scrollbar {
  display: none;
  width: 0;
  background: transparent;
}

.movie-trending {
  position: absolute;
  width: 10rem;
  -webkit-box-shadow: 0.5rem 0.25rem 1rem rgba(0, 0, 0, 0.25);
          box-shadow: 0.5rem 0.25rem 1rem rgba(0, 0, 0, 0.25);
  border-radius: 1.5rem;
  -webkit-transition: all 0.35s ease-in;
  -o-transition: all 0.35s ease-in;
  transition: all 0.35s ease-in;
  cursor: pointer;
  background: transparent;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

